.App {
  height: 100%;
  width: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.backgroundContainer {
  position: relative; /* Это необходимо, чтобы создать контекст позиционирования для абсолютно позиционированных элементов внутри него */
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.backgroundImage {
  max-width: 100%;
  max-height: 100%;
  z-index: 1;
}

.fogGif {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: 36.5%;
  z-index: 2;
  opacity: 0.4;
}
